import React, { useMemo } from 'react';
import _ from 'lodash';
import { useContextSelector } from 'use-context-selector';

import styles from './NavFooter.module.scss';
import Progress from '../UI/Progress/Progress';
import Button from '../UI/Button/Button';
import Audio from '../Audio/Audio';
import Label from '../context/Label/Label';
import { UIContext } from '../context/UI/UIContext';
import Tooltip from '../UI/Tooltip/Tooltip';
import useLabel from '../context/Label/useLabel';

type NavFooterProps = {
  globalState: any;
};

function NavFooter({ globalState }: NavFooterProps) {
  const {
    goNextPage,
    goPrevPage,
    getColorScheme,
    getCoursePercentage,
    getCurrentPage,
    course,
    getTracking,
    isInFirstPage,
    isInLastPage,
    isNavigationModeFree,
    onPageCompleted,
  } = globalState;
  const { primary } = getColorScheme();
  const currentPage = getCurrentPage();
  const pageTracking = getTracking(currentPage.resourceId);
  const stopBlinkNext = useContextSelector(UIContext, (c) => c.stopBlinkNext);
  const blinkNext = useContextSelector(UIContext, (c) => c.blinkNext);
  const startBlinkNext = useContextSelector(UIContext, (c) => c.startBlinkNext);
  const { plainLabel } = useLabel();

  const hasAudio = !_.isEmpty(currentPage?.audio?.name);

  const showNavButtons = useMemo(
    () => _.size(course?.pages) > 1,
    [course?.pages]
  );

  const _isInLastPage = isInLastPage();
  const nextPageDisabled = useMemo(
    () => (!pageTracking.completed && !isNavigationModeFree) || _isInLastPage,
    [isNavigationModeFree, pageTracking.completed, _isInLastPage]
  );

  function NextButton({ disabled }: { disabled: boolean }) {
    return (
      <Button
        onClick={() => {
          goNextPage();
          stopBlinkNext();
        }}
        globalState={globalState}
        className={[
          styles.NavFooter__button,
          styles.NavFooter__nextButton,
          blinkNext ? styles.NavFooter__button__blink : '',
        ].join(' ')}
        disabled={disabled}
        testId="next-button"
      >
        <Label>next</Label>
      </Button>
    );
  }

  return (
    <div className={styles.NavFooter} style={{ backgroundColor: primary }}>
      <Progress
        percent={getCoursePercentage()}
        showText
        color="#ffffff"
        className={styles.NavFooter__progress}
      />
      {hasAudio && (
        <div className={styles.NavFooter__audio}>
          <Audio
            url={`${course.baseURL}/${currentPage?.audio.name}`}
            onEnded={() => onPageCompleted(() => startBlinkNext())}
          />
        </div>
      )}
      {showNavButtons ? (
        <div className={styles.NavFooter__navbuttons} data-testid="navButtons">
          <Button
            onClick={() => {
              goPrevPage();
              stopBlinkNext();
            }}
            globalState={globalState}
            className={styles.NavFooter__button}
            disabled={isInFirstPage()}
          >
            <Label>previous</Label>
          </Button>
          {!pageTracking.completed && hasAudio && !isNavigationModeFree ? (
            <Tooltip
              tooltip={plainLabel(
                'Hear the audio until the end to advance on this course.'
              )}
              width="150"
            >
              <NextButton disabled={nextPageDisabled} />
            </Tooltip>
          ) : (
            <NextButton disabled={nextPageDisabled} />
          )}
        </div>
      ) : null}
    </div>
  );
}

export default NavFooter;
